<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row class="dt-title"
             type="flex"
             align="center">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />

      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">未阅人员</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <!-- 工单1
      <van-row>
        <div class="dt-readsituation1">
          <div class="dt-read-title"> 已阅人员</div>
          <div class="dt-line"></div>
          <div class="dt-name"> {{form.name}}</div>
        </div>
      </van-row> -->
      <!-- 工单2 -->
      <van-row>
        <div class="dt-readsituation1">
          <div class="dt-read-title"> 未阅人员</div>
          <div class="dt-line"></div>
          <div class="dt-name">
            <span v-for="item in employeeList"
                  :key="item">{{item.ReceiveUserName}}、</span>
          </div>
        </div>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetTaskuser,  // 任务下发记录查询
} from "@/api/testApi"
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      employeeList: []
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getTaskuser()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 任务下发记录查询
    getTaskuser () {
      let notReadInfo = {
        TaskId: this.$route.query.ID,
        ReadStatus: 0,  // 0未阅，1已阅
        PageSize: 100,
        PageIndex: 1
      }
      GetTaskuser(notReadInfo).then(response => {
        if (response.Data.ContentList) {
          this.employeeList = response.Data.ContentList
        }
      })
    },

    // 跳转待审核页面
    ToDoTask () {
      this.$router.push({ name: 'ToDoTask' })
    },
    // 跳转已阅
    HaveRead () {
      this.$router.push({ name: 'HaveRead' })
    },
    // 已审核
    BPAudited () {
      this.$router.push({ name: 'BPAudited' })
    },
    back () {
      this.$store.commit("active", 0)
      javascript: history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
@import "../../../styles/dimens";
//@import url(); 引入公共css类
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
}
.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  background: #f6f8fa;
  padding-top: px2rem(14);
  .dt-readsituation1 {
    display: flex;
    flex-direction: column;
    font-size: px2rem(12);
    text-align: left;
    line-height: px2rem(20);
    margin: px2rem(6) px2rem(12);
    background-color: #ffffff;
    border-radius: px2rem(5);

    .dt-read-title {
      height: px2rem(38);
      font-size: px2rem(15);
      text-align: center;
      line-height: px2rem(38);
      color: #4c4c4c;
    }
    .dt-line {
      width: px2rem(326.5);
      background-color: #edf1f5;
      height: px2rem(0.5);
      margin: 0 auto;
    }
    .dt-name {
      font-size: px2rem(15);
      color: #707070;
      line-height: px2rem(20);
      font-family: PingFang-SC-Regular;
      padding: px2rem(12) px2rem(23) 0 px2rem(12.5);
      margin-bottom: px2rem(12);
    }
  }
}
</style>
